import React from 'react';
import './About.css';

const content = [
    `I'm passionate about technologies and programming and now I have six months of experience as a front-end developer in a fast-pased, chaotic work environment.`,
    `I ride my bycicle from home to work every day unless it's raining or snowing.`,
    `I spend my free time to study and learn new things. I like to read books.`,
    `I enjoy watching streamers playing games in twitch.tv.`,
    `I'm listening to music all the time. While riding, working, relaxing, cleaning the house or eating, sometimes when sleeping.`,

]


// calculate experience days here --- for fun

class About extends React.Component {
    render() {
        return (
            <section className='section about'>
                <h1 className='section__title'>My passions and personality</h1>
                {content.map((e, i) => <p className={`section__p about__p about__p-${i}`}>{e}</p>)}
            </section>
        )
    }
}

export default About;


